@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap");

body {
  margin: 0;
  font-family: Poppins;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
    90deg,
    rgb(255, 253, 208),
    rgb(136, 173, 151)
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.board {
  text-align: center;
  margin: auto;
}

.board-row {
  display: flex;
  justify-content: center;
}

.btn {
  height: 120px;
  width: 120px;
  font-size: 50px;
}

.App {
  width: 80%;
  margin: auto;
}

.header {
  text-align: center;
  font-size: 40px;
  text-shadow: 2px 2px 4px #bbb;
  cursor: crosshair;
}

.hide {
  display: none;
}

input {
  margin: 10px;
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #ff0c0c;
  outline: none;
  width: 320px;
  background: transparent;
  color: #fff;
  font-size: 20px;
  transition: 0.3s;
}

input::placeholder {
  color: #fff;
}

input:focus {
  border-color: rgb(25, 192, 243);
}

.menu{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 520px;
  min-height: 200px;
  background: #4e9ad8;
  text-align: center;
  margin: 50px auto;
  border-radius: 10px;
}

.enabled {
  background: linear-gradient(90deg, rgb(24, 163, 24), rgb(165, 241, 162));
  border: none;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
  color: #fff;
  padding: 16px;
  transition: 0.3s;
}

.disabled {
  background: linear-gradient(90deg, rgb(187, 187, 187), rgb(255, 255, 255));
  border: none;
  border-radius: 4px;
  cursor: default;
  outline: none;
  color: #000;
  padding: 16px;
  transition: 0.3s;
}

.error{
  font-size: 10px;
  display: flex;
  justify-content: flex-start;
  margin-left: 72px;
  color: red;
}

